<script>
  import SearchBox from "./components/common/search-box"
  import chatPage from "./views/chat/chat"
  import CustomerService from "./components/common/customerService";
  export default {
    name: "App",
    components: {CustomerService},
    data() {
      return {
          imgUrl: sessionStorage.getItem('imgUrl'),
          cnt: 0,
        show_chat: 0,
        vip:sessionStorage.getItem("vip").toLocaleUpperCase(),
        username:sessionStorage.getItem('username')
      }
    },

    methods: {
      quite(){
        sessionStorage.removeItem('username')
        location.reload();
      },
      getMsgFormSon(data){
          console.log(data)
      },
      showChat(){
        this.$refs.child.showChatS();
        this.show_chat ^= 1;
      },
      handleCommand(command) {
        if(command == 'e'){
            sessionStorage.clear();
           this.$store.state.logining = false;
            location.reload();
        }
        if(command == 'a'){
          this.$router.replace('/information');
        }
      },
      fun1(){
        this.$router.replace("/home");
      },
      fun2(){
        this.$router.replace("/about");
      },
      handleSelect(key, keyPath) {

        /*console.log(sessionStorage.getItem('nickName') == null);
        console.log(key == "11");*/
       if(key == "11" && sessionStorage.getItem('nickName') == null){
         //alert();
         this.$router.replace("/login");
       }
       if(key == "1"){
         this.$router.replace("/home");
       }
       if(key == '2'){
         this.$router.replace("/video");
       }
       if(key[0] == '3'){
         this.$router.replace("/questionSave");
       }
       if(key == '10-1'){
         this.$router.replace("/editor");
       }
       if(key == '10-2'){
         this.$router.replace("/forum");
       }
       if(key == '10-3'){
         this.$router.replace("/uploadVideo");
       }
       if(key == '4'){
         this.$router.replace("/questionBank");
       }
       if(key == '5'){
         this.$router.replace("/blog");
       }
       if(key == '6-1' || key == '6-2'){
         this.$router.replace("/recruit");
       }
       if(key == '7'){
         this.$router.replace("/forums");
       }
       if(key == '8'){
         this.$router.replace("/vip");
       }
       if(key == '9'){
         this.$router.replace("/dynamic");
       }

      }
    },
    comments: {

    },
    components: {
       SearchBox,
      chatPage,
      CustomerService
    },
    created () {
      this.username = sessionStorage.getItem('username')
      setTimeout(() => {
        window.L2Dwidget.init({
          "model": { "jsonPath":"https://unpkg.com/live2d-widget-model-miku@1.0.5/assets/miku.model.json", "scale": 1, "hHeadPos":0.5, "vHeadPos":0.618 },
          "display": { "position": "left", "width": 100, "height": 300, "hOffset": 0, "vOffset": 0 },
          "mobile": { "show": true, "scale": 0.5 },
          "react": { "opacityDefault": 0.7, "opacityOnHover": 0.2 }
        });
      }, 1000)
    }
  }
</script>
